var WaterPolo = function(selector, userOptions) {
  "user strict";

  userOptions = userOptions || {};

  var options = {
    //å¤è¾¹è·
    wrapW: 3,

    //canvaså±æ§
    cW: 130,
    cH: 130,
    lineWidth: 2,

    //æ¶²é¢ä½ç½® ç¾åæ¯è¡¨ç¤º
    baseY: 20,

    //æ¶²é¢èµ·å§ä½ç½®
    nowRange: 0,

    //çº¿æ¡é¢è²
    lineColor: "rgb(176,204,53)",
    //ä¸å±é¢è²
    oneColor: "rgba(176,204,53,.6)",

    //ä¸å±é¢è²
    twoColor: "rgba(176,204,53,.4)",

    //ä¸å±æ³¢æµªå®½åº¦ï¼æ°è¶å°è¶å®½
    oneWaveWidth: 0.06,

    //ä¸å±æ³¢æµªå®½åº¦
    twoWaveWidth: 0.06,

    //ä¸å±æ³¢æµªé«åº¦ï¼æ°è¶å¤§è¶é«
    oneWaveHeight: 4,

    //ä¸å±æ³¢æµªé«åº¦
    twoWaveHeight: 4,

    //ä¸å±æ³¢æµªxè½´åç§»é
    oneOffsetX: 10,

    //ä¸å±æ³¢æµªxè½´åç§»é
    twoOffsetX: 20,

    //æ³¢æµªæ»å¨éåº¦ï¼æ°è¶å¤§è¶å¿«
    speed: 0.2
  };

  var canvas = null,
    ctx = null,
    W = null,
    H = null;

  Object.defineProperty(this, "options", {
    get: function() {
      return options;
    },
    set: function(value) {
      mergeOption(value, options);
    }
  });

  //åæ°æ··åç¸å½äº$.extend([old],[new])
  var mergeOption = function(userOptions, options) {
    Object.keys(userOptions).forEach(function(key) {
      options[key] = userOptions[key];
    });
  };

  //çææ¶²é¢
  var makeLiquaid = function(ctx, xOffset, waveWidth, waveHeight, color) {
    ctx.save();
    var points = []; //ç¨äºå­æ¾ç»å¶Sinæ²çº¿çç¹
    ctx.beginPath();
    //å¨xè½´ä¸åç¹
    for (var x = 0; x < options.cW; x += 20 / options.cW) {
      //æ­¤å¤åæ (x,y)çåç¹ï¼ä¾é å¬å¼ âæ¯å¹é«*sin(x*æ¯å¹å®½ + æ¯å¹åç§»é)â
      var y = -Math.sin(x * waveWidth + xOffset);

      //æ¶²é¢é«åº¦ç¾åæ¯æ¹å
      var dY = options.cH * (1 - options.nowRange / 100);

      points.push([x, dY + y * waveHeight]);
      ctx.lineTo(x, dY + y * waveHeight);
    }
    //å°é­è·¯å¾
    ctx.lineTo(options.cW, options.cH);
    ctx.lineTo(0, options.cH);
    ctx.lineTo(points[0][0], points[0][1]);
    ctx.fillStyle = color;
    ctx.fill();
    ctx.restore();
  };

  //åå§åè®¾ç½®

  var init = function() {
    mergeOption(userOptions, options);

    canvas = document.getElementById(selector);
    ctx = canvas.getContext("2d");

    canvas.width = options.cW;
    canvas.height = options.cH;

    ctx.lineWidth = options.lineWidth;

    //åå±æ§
    var r = options.cH / 2; //åå¿
    var cR = r - 6; //ååå¾ å³å®åçå¤§å°

    var drawCircle = function(ctx) {
      ctx.beginPath();
      ctx.strokeStyle = options.lineColor;
      ctx.arc(r, r, cR + options.wrapW, 0, 2 * Math.PI);
      ctx.stroke();
      ctx.beginPath();
      ctx.arc(r, r, cR, 0, 2 * Math.PI);
      ctx.clip();
    };
    drawCircle(ctx); //ç»å

    (function drawFrame() {
      window.requestAnimationFrame(drawFrame);

      ctx.clearRect(0, 0, options.cW, options.cH);

      //é«åº¦æ¹åå¨ç»åæ°
      var tmp = 1;
      if (options.nowRange <= options.baseY) {
        options.nowRange += tmp;
      }

      if (options.nowRange > options.baseY) {
        options.nowRange -= tmp;
      }
      makeLiquaid(
        ctx,
        options.oneOffsetX,
        options.oneWaveWidth,
        options.oneWaveHeight,
        options.oneColor
      );
      makeLiquaid(
        ctx,
        options.twoOffsetX,
        options.twoWaveWidth,
        options.twoWaveHeight,
        options.twoColor
      );

      options.oneOffsetX += options.speed;
      options.twoOffsetX += options.speed;
    })();
  };
  init();
};

export default WaterPolo